import { authAPI, secAuthAPI } from "../api/api";
import { stopSubmit } from "redux-form";
import { setUserSubscriptions } from "./profile-reducer";

const SET_USER_DATA = 'samurai-network/auth/SET_USER_DATA';
const SET_CAPTCHA_URL = 'SET_CAPTCHA_URL';
const SET_NICKNAME = 'SET_NICKNAME';

let initialState = {
    userId: null,
    email: null,
    nickname: null,
    isAuth: false,
    captchaUrl: null
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload
            }
        case SET_NICKNAME:
            return {
                ...state,
                nickname: action.nickname
            }

        case SET_CAPTCHA_URL:
            return {
                ...state,
                captchaUrl: action.captchaUrl
            }

        default:
            return state;
    }
}


export const setAuthUserData = (nickname, userId, email, isAuth) => ({
    type: SET_USER_DATA, payload:
        { userId, email, nickname, isAuth }
})

export const setNickname = (nickname) => ({ type: SET_NICKNAME, nickname })

export const setCaptchaUrl = (captchaUrl) => ({
    type: SET_CAPTCHA_URL, captchaUrl

})

export const getAuthUserData = () => async (dispatch) => {
    let response = await authAPI.me();

    if (response.data.resultCode === 0) {
        let { id, email, nickname, subscriptions } = response.data;
        dispatch(setAuthUserData(nickname, id, email, true))
        dispatch(setUserSubscriptions(subscriptions))
    }
}

export const login = (email, password, rememberMe, captcha) => async (dispatch) => {
    try {
        let response = await authAPI.login(email, password, rememberMe, captcha)
        if (response.data.resultCode === 0)
            dispatch(getAuthUserData())
    }
    catch(err) {
        dispatch(stopSubmit("login", { _error: err.response.data.message }))
    }
}

export const logout = () => async (dispatch) => {
    let response = await authAPI.logout();

    if (response.data.resultCode === 0) {
        dispatch(setAuthUserData(null, null, null, false));
    }
}

export const captchaThunk = () => async (dispatch) => {
    let response = await secAuthAPI.requestCaptcha();
    dispatch(setCaptchaUrl(response.data.url))
}

export default authReducer;